.main-nav {
  background-color: #f1faff;
  position: relative;
  min-height: 50px;
}

@media (min-width: @tablet-width) {
  .main-nav {
    padding: 0 23px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    padding: 0 30px;
    min-height: 72px;
  }
}

.main-nav--fixed {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;
  transition: all 0.5s ease;
}

@media (min-width: @tablet-width) {
  .main-nav__wrapper {
    display: flex;
  }
}

@media (min-width: @desktop-width) {
  .main-nav__wrapper {
    max-width: 1380px;
    margin: 0 auto;
  }
}

.main-nav__link {
  color: #e3f5fe;
  text-transform: uppercase;
  font-weight: 800;
  line-height: 101.4%;
  display: inline-block;
  padding: 16px 26px;
  font-size: 20px;
}

@media (min-width: @tablet-width) {
  .main-nav__link {
    padding: 29px 27px 28px;
    color: #10273b;
    font-size: 18px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 0;
      background-color: #66b2f9;
      z-index: -1;
      transition: height 0.2s ease-in-out;
    }

    &:hover::before {
      height: 100%;
    }

    &:hover {
      color: #e3f5fe;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-nav__link {
    font-size: 20px;
    padding: 24px 42px 28px 41px;
  }
}

.main-nav__item {
  border-bottom: 1px solid rgba(227, 245, 254, 0.5);
  min-width: 200px;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .main-nav__item {
    min-width: max-content;
    border-bottom: none;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  content: "";
  top: 50px;
  left: 0;
  z-index: 2;
  width: 100%;
  background-color: #2b63a2;
}

@media (min-width: @tablet-width) {
  .main-nav__list {
    flex-direction: row;
    justify-content: flex-end;
    margin-left: auto;
    position: static;
    padding: 0;
    background-color: #f1faff;
  }
}

.main-nav__logo {
  display: flex;
  align-items: center;
  padding: 12px 25px 13px;
  color: #10273b;
}

@media (min-width: @tablet-width) {
  .main-nav__logo {
    padding: 18px 27px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav__logo {
    padding: 15px 40px;
  }
}

.main-nav__toggle {
  width: 30px;
  height: 25px;
  display: block;
  background-color: transparent;
  position: absolute;
  top: 18px;
  right: 25px;
  z-index: 2;
  cursor: pointer;
  border: none;
  padding: 0;

  &:focus {
    outline: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 3px;
  background-color: #10273b;
  top: -2px;
  left: 0;
  box-shadow: 0 9px 0 0 #10273b, 0 18px 0 0 #10273b;
}

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  background-color: #66b2f9;
  box-shadow: 0 9px 0 0 #66b2f9, 0 18px 0 0 #66b2f9;
  outline: none;
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 33px;
    height: 2px;
    top: 9px;
    right: -2px;
    background-color: #10273b;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  background-color: #66b2f9;
  outline: none;
}

@media (min-width: @tablet-width) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__toggle {
    display: block;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .main-nav__image {
    width: 139px;
    height: 39px;
  }
}

.main-nav--opened .main-nav__list {
  padding-top: 7px;
  padding-bottom: 29px;
}
